
@import "assets/css/normalize.css";
@import "assets/css/pocketgrid.css";
html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
@font-face {
  font-family: 'Segment7Standard';
  src: url('../static/fonts/Segment7Standard.otf') format('opentype');
  font-weight: normal;
  font-style: italic;
}
html {
  background-color: #26292e;
}
#app {
  width: 1024px;
  margin: 0 auto;
}
.warning-strip {
  background: repeating-linear-gradient(-45deg, #bfa50c, #bfa50c 15px, #2d2d2d 15px, #2d2d2d 31px), url(../static/img/grunge_overlay.png) center repeat;
  background-blend-mode: multiply;
}
.danger-strip {
  background: repeating-linear-gradient(-45deg, #ee321b, #ee321b 15px, #390a04 15px, #390a04 31px), url(../static/img/grunge_overlay.png) center repeat;
  background-blend-mode: multiply;
}
i.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  position: relative;
  top: 0.2em;
  margin-right: 0.2em;
  background-size: contain;
  background-position: center;
}
i.icon.check,
i.icon.normal {
  background-image: url('assets/svg/icons/check.svg');
}
i.icon.exclamation-orange,
i.icon.warning {
  background-image: url('assets/svg/icons/exclamation_orange.svg');
}
i.icon.exclamation-red,
i.icon.critical {
  background-image: url('assets/svg/icons/exclamation_red.svg');
}



