
.range-slider {
  margin: 0 10px 15px;
}
.range-slider-track {
  width: auto;
  height: 30px;
  margin: 0 auto;
  position: relative;
  cursor: e-resize;
}
.range-slider-track:before {
  content: '';
  display: block;
  position: absolute;
  top: 12px;
  left: 0;
  width: 100%;
  height: 6px;
  background-color: #252525;
}
.range-slider-track .dragger {
  display: block;
  width: 10px;
  height: inherit;
  position: relative;
  z-index: 2;
  background-color: #191c23;
  cursor: inherit;
}
.range-slider-vertical {
  display: inline-block;
  vertical-align: middle;
  margin: 10px 15px 10px 0;
}
.range-slider-vertical .range-slider-track {
  cursor: n-resize;
  width: 30px;
  height: 100%;
}
.range-slider-vertical .range-slider-track:before {
  top: 0;
  right: auto;
  left: 12px;
  width: 6px;
  height: 100%;
}
.range-slider-vertical .range-slider-track .dragger {
  width: inherit;
  height: 10px;
}



