
@import "../../../assets/css/normalize.css";
@import "../../../assets/css/pocketgrid.css";
html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
@font-face {
  font-family: 'Segment7Standard';
  src: url('../../../../static/fonts/Segment7Standard.otf') format('opentype');
  font-weight: normal;
  font-style: italic;
}
html {
  background-color: #26292e;
}
#app {
  width: 1024px;
  margin: 0 auto;
}
.warning-strip {
  background: repeating-linear-gradient(-45deg, #bfa50c, #bfa50c 15px, #2d2d2d 15px, #2d2d2d 31px), url(../../../../static/img/grunge_overlay.png) center repeat;
  background-blend-mode: multiply;
}
.danger-strip {
  background: repeating-linear-gradient(-45deg, #ee321b, #ee321b 15px, #390a04 15px, #390a04 31px), url(../../../../static/img/grunge_overlay.png) center repeat;
  background-blend-mode: multiply;
}
i.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  position: relative;
  top: 0.2em;
  margin-right: 0.2em;
  background-size: contain;
  background-position: center;
}
i.icon.check,
i.icon.normal {
  background-image: url('../../../assets/svg/icons/check.svg');
}
i.icon.exclamation-orange,
i.icon.warning {
  background-image: url('../../../assets/svg/icons/exclamation_orange.svg');
}
i.icon.exclamation-red,
i.icon.critical {
  background-image: url('../../../assets/svg/icons/exclamation_red.svg');
}
html {
  background: #26292e;
}
.content {
  padding: 15px;
  background: #2f3440;
  color: #ebebeb;
}
h1 {
  margin: 0 0 0.25em;
}
h1 .secondary {
  font-size: 0.5em;
}
h1 .secondary.critical {
  color: #ff4629;
}
h1 .secondary.warning {
  color: #f7b233;
}
h1 .secondary.normal {
  color: #37ee37;
}
.label {
  padding: 6px 10px;
  border-radius: 4px;
  display: inline-block;
  text-align: center;
  font-family: monospace;
  font-size: 1.25em;
  color: #ebebeb;
  background-color: #242831;
}
.label.secondary {
  color: #6b6b6b;
  background-color: #262a34;
}
nav {
  padding: 5px;
  background: repeating-linear-gradient(-45deg, #bfa50c, #bfa50c 15px, #2d2d2d 15px, #2d2d2d 31px), url(../../../../static/img/grunge_overlay.png) center repeat;
  background-blend-mode: multiply;
}
nav .nav-item-wrapper {
  padding: 2px;
}
nav .nav-item-wrapper,
nav .nav-item-wrapper:focus {
  outline: none;
}
nav .nav-item,
nav .status {
  display: block;
  border-radius: 6px;
  color: #ebebeb;
  background-color: #191c23;
  text-decoration: none;
}
nav .nav-item,
nav .status,
nav .nav-item:focus,
nav .status:focus {
  outline: none;
}
nav .nav-item {
  padding: 10px;
  text-align: center;
}
nav .nav-item.router-link-exact-active {
  color: #ebebeb;
  background-color: #454c5d;
}
nav .status {
  margin-top: 4px;
  border: 2px solid #454c5d;
  padding: 4px;
  font-size: 0.8em;
}
nav .status.critical {
  border-color: #704f4b;
  background-color: #ff4629;
}
nav .status.warning {
  border-color: #696255;
  background-color: #c28c2f;
}
nav .status.normal {
  border-color: #4c5e4c;
  background-color: #2cb12c;
}
/* If we didn't use this wrapper and instead used margin-top on .alerts, there'd be a gap between
 .status and .alerts, closing the popup if moving the pointer down into it */
.alerts-wrapper {
  position: absolute;
  z-index: 100;
  padding-top: 8px;
  margin-left: -6px;
}
.alerts {
  border: 2px solid #191c23;
  padding: 10px;
  border-radius: 6px;
  box-shadow: 0 0 10px 2px rgba(25, 28, 35, 0.75);
  background-color: #2f3440;
  display: none;
}
.alerts.visible {
  display: block;
}
.alerts ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.alerts ul li + li {
  margin-top: 0.5em;
}



